export default {"/Resources":"<div class=\"other-resources\">\n\t<h1>Other Resources</h1>\n\n\t<h2>Learning Lua</h2>\n\n\t<ul>\n\t\t<li>\n\t\t\t<a target=\"_blank\" rel=\"noopener noreferrer\" href=\"https://www.tutorialspoint.com/lua/\">TutorialsPoint Lua Guide</a><br />\n\t\t\tThis is a practical guide for getting up and running with Lua's basic syntax and features quickly.\n\t\t</li>\n\n\t\t<li>\n\t\t\t<a target=\"_blank\" rel=\"noopener noreferrer\" href=\"https://www.lua.org/pil/contents.html\">Programming in Lua</a><br />\n\t\t\tAuthored by Lua's chief language architect for Lua 5.0, this is a thorough, academic approach to the language's features.\n\t\t</li>\n\t</ul>\n\n\t<h2>StepMania GitHub Wiki</h2>\n\t<ul>\n\t\t<li>\n\t\t\t<a target=\"_blank\" rel=\"noopener noreferrer\" href=\"https://github.com/stepmania/stepmania/wiki/Theming\">\n\t\t\t\tTheming\n\t\t\t</a>\n\t\t</li>\n\t\t<li>\n\t\t\t<a target=\"_blank\" rel=\"noopener noreferrer\" href=\"https://github.com/stepmania/stepmania/wiki/Actor-Definitions\">\n\t\t\t\tActor Definitions\n\t\t\t</a>\n\t\t</li>\n\t\t<li>\n\t\t\t<a target=\"_blank\" rel=\"noopener noreferrer\" href=\"https://github.com/stepmania/stepmania/wiki/Noteskins\">\n\t\t\t\tNoteSkins\n\t\t\t</a>\n\t\t</li>\n\t</ul>\n\n\t<h2>Lua 5.1 Reference</h2>\n\t<ul>\n\t\t<li>\n\t\t\t<a target=\"_blank\" rel=\"noopener noreferrer\" href=\"https://www.lua.org/manual/5.1/\">Lua 5.1 Reference Manual</a><br />\n\t\t\tThis is the official reference manual for Lua 5.1, which StepMania 5 and 5.1 use.  It is complete,\n\t\t\tthough not easy to follow along with.\n\t\t</li>\n\t</ul>\n</div>","/":"<h1>Lua for StepMania 5</h1>\n\n<p>\n\tHello there!  In these pages, you&apos;ll find my <strong>Lua for SM5</strong> guide, comprised of various chapters organized under the headers <span class=\"d-none d-md-inline d-lg-inline d-xl-inline\">on the left</span> <span class=\"d-xl-none d-lg-none d-md-none\">at the bottom</span> of this page.\n</p>\n<p>\n\tIf you&apos;re unsure of where to start, check out the chapter on <a data-component=\"Link\" href=\"/Introduction/Foreword\">What Are Actors?</a> to get a sense of how Lua files are organized in StepMania 5.\n</p>\n<p>\n\tStepMania 5&apos;s complete Lua API documentation can be found in its Lua.xml file, which ships with releases of StepMania 5 in the <code>./Docs/Luadoc/</code> directory and can be viewed in most web browsers. I have also hosted a copy as part of this website: <a data-component=\"Link\" href=\"/LuaAPI\">Lua For SM5: Lua API</a>.\n</p>\n<p>\n\tYou can find additional resources for learning about Lua and SM5 scripting on the <a data-component=\"Link\" href=\"/Resources\">Other Resources</a> page.\n</p>","/Actors/Actor":"<h1>Actor</h1>\n\n<p>\n   The <strong>Actor</strong> class is the most generic and lightweight StepMania Actor; all other Actors inherit from it (directly or eventually). Using an Actor object directly is not generally helpful; it can't <em>do</em> very much. The <code>LoadActor()</code> helper function <a data-component=\"Link\" href=\"LoadActor\">discussed later</a> is far more flexible and useful!\n</p>\n\n<p>\n   Still, there are times when <strong>Actor</strong> objects can be useful. For example, the Simply Love SM5 port uses them when we need a hook into some message broadcast by the engine but don't need an entire ActorFrame.\n</p>\n\n<p>\n   In theming example below, we are using an <code>Actor</code> to listen for the <code>StartTransitioningCommand</code> to be broadcast by the engine when a screen is transitioning out (and preparing to initialize the next screen). When that occurs, we make a call to save the theme's preferences, assuming the theme is already using the <a href=\"https://github.com/stepmania/stepmania/blob/5_1-new/Themes/_fallback/Scripts/02%20ThemePrefs.lua\">ThemePrefs system</a>.\n</p>\n\n<span class=\"CodeExample-Title\">Example using an Actor directly:</span>\n<pre><code class=\"lua\">\nreturn Def.Actor{\n   StartTransitioningCommand=function(self)\n      ThemePrefs.Save()\n   end\n}\n</code></pre>\n\n<p>\n   For another example, see: <a href=\"https://github.com/quietly-turning/Simply-Love-SM5/blob/00fdf7112b0050d229679c39d777dfca5f0bb11a/BGAnimations/ScreenGameplay%20overlay/ReceptorArrowsPosition.lua#L25-L40\">./Simply Love/BGAnimations/ScreenGameplay overlay/ReceptorArrowsPosition.lua</a>\n</p>\n","/Actors/ActorFrameTexture":"<h1>ActorFrameTexture</h1>\n\n<p><em>ActorFrameTexture</em> actors can be used to take what would otherwise be an ActorFrame of children Sprites, BitmapTexts, etc., and render them directly to a unique texture that can be loaded into a single Sprite actor.  At that point, the original ActorFrameTexture can be cut out of the render pipeline with a <code>visible(false)</code> command, and StepMania will have that many fewer actors to process every draw cycle.</p>\n\n<p>This is a more abstract and advanced topic, but it can help cut down on the overhead of having StepMania keep track of many, many actors where a single sprite might suffice.</p>\n\n<p>For now, please refer to <a href=\"https://github.com/stepmania/stepmania/blob/master/Docs/Themerdocs/Examples/Example_Actors/ActorFrameTexture.lua\">Matt Gardner&apos;s writeup</a>.</p>","/Actors/ActorMultiVertex":"<h1>ActorMultiVertex</h1>\n\n<p> What <code>Def.Quad</code> does for quadrilaterals, <em>ActorMultiVertex</em> does for arbitrary polygons.  For documentation, please refer to <a href=\"https://github.com/stepmania/stepmania/blob/master/Docs/Themerdocs/ScreenAMVTest%20overlay.lua\">kyzentun&apos;s writeup</a>.</p>\n\n<p>An <em>ActorMultiVertex</em> actor has seven possible <a data-component=\"Link\" href=\"/LuaAPI#Enums-DrawMode\">DrawModes</a>.  Each DrawMode has a distinct visual style and each will require you to format your table of vertex data a little differently.</p>\n\n<p>For example, calling <code>self:SetDrawState({Mode=\"DrawMode_Quads\"})</code> on an ActorMultiVertex will cause it to render every four vertices as a quadrilateral.  Simply Love uses an <em>ActorMultiVertex</em> in <code>DrawMode_Quads</code> to <a href=\"https://github.com/quietl-turning/Simply-Love-SM5/blob/01c5764200ac790fa7d7e4a539afb402ba33cc16/BGAnimations/ScreenEvaluation%20common/PerPlayer/ScatterPlot.lua#L55-L79\">render its ScatterPlot</a> on ScreenEvaluation.  Each judgment from Gameplay <a href=\"https://i.imgur.com/JK5Li2w.png\">is rendered</a> as a quadrilateral within a single <code>Def.ActorMultiVertex</code> actor.  This is more efficient than drawing additional <code>Def.Quad</code> actors for each judgment.</p>\n\n<p>Another <em>ActorMultiVertex</em> DrawMode is <code>DrawMode_LineStrip</code>, in which your table of vertex data will be rendered as a single, continuous line.  An example of <code>DrawMode_LineStrip</code> in action can be seen in <a href=\"https://www.youtube.com/watch?v=hKd4xkULxFk\">this scripted simfile</a> from the U.P.S. 2 pack.</p>","/Actors/BitmapText":"<h1>BitmapText</h1>\n\n<p><em>BitmapText</em> Actors are used to display text on the screen.</p>\n\n<p>As Lua tables, they have two unique elements, <code>Font</code> and <code>Text</code>.  <code>Font</code> should be set to any font in the current or <em>_fallback</em> theme&apos;s <em>./Font</em> directory. <code>Text</code> should be set to a string value, but it can be left empty or not declared at all if you intend to programmatically set the text later with the <code>settext()</code> method.</p>\n\n<p>Here is a very simple example that would display the text \"Hello SM5!\"</p>\n<span class=\"CodeExample-Title\">Simple BitmapText example</span>\n<pre><code class=\"lua\">\nDef.BitmapText{\n   Font=\"Common normal\",\n   Text=\"Hello SM5!\",\n   InitCommand=function(self) self:Center() end\n}\n</code></pre>\n\n<p>StepMania 5&apos;s <em>_fallback</em> theme defines a helper function, <code>LoadFont()</code>, that can be used to simplify the above code somewhat.</p>\n\n<span class=\"CodeExample-Title\">BitmapText example using LoadFont()</span>\n<pre><code class=\"lua\">\nLoadFont(\"Common normal\")..{\n   Text=\"Hello SM5!\",\n   InitCommand=function(self) self:Center() end\n}\n</code></pre>\n\n<hr />\n\n<h3>Loading Custom Fonts within a Scripted Simfile</h3>\n\n<p>Note that the <code>LoadFont()</code> helper function can only be used to load BitmapText fonts contained within the current theme&apos;s <code>./Fonts/</code> directory.\n If you are working within the context of a <a data-component=\"Link\" href=\"/Introduction/Mod-Chart-Setup\">scripted simfile</a> and are trying to load a custom BitmapText font that is located within the simfile directory itself, you can do so like this:</p>\n\n<span class=\"CodeExample-Title\">loading a custom BitmapText from within a simfile</span>\n<pre><code class=\"lua\">\nDef.BitmapText{\n   File=GAMESTATE:GetCurrentSong():GetSongDir()..\"Fonts/helvetica neue/_helvetica neue 20px.ini\",\n   Text=\"Hello, World!\",\n   InitCommand=function(self) self:diffuse(1,1,1,1):Center():shadowlength(0.75) end\n}\n</code></pre>\n\n<p>The program used to turn ttf fonts into spritesheets that StepMania can use comes bundled with release versions of SM5 for Windows in the <code>./Program/</code> folder, and is titled <strong>Texture Font Generator.exe</strong>.\n\tIt is only available for Windows.</p>\n\n<p>When loading custom fonts within scripted simfiles like this, it may be necessary to modify the ini file that <em>Texture Font Generator.exe</em> outputs for character widths to be respected.</p>\n\n\n\n<span class=\"CodeExample-Title\">the ini file Texture Font Generator will give you</span>\n<pre><code class=\"ini\">\n[common]\nBaseline=20\n# etc.\n\n[main]\nLine  0= !\"$%&'()*+,-.\nLine  1=/0123456789:;<\nLine  2=>?@ABCDEFGHIJKL\nLine  3=MNOPQRSTUVWXYZ\nLine  4=\\\\]^_abcdefghij\n# etc.\n\n0=6\n1=5\n2=7\n3=11\n4=11\n5=18\n# etc.\n</code></pre>\n\n\n<p>If you find that character widths seem incorrect, you may be able to fix this issue by removing the <code>[main]</code> section from the font's ini file so that everything is defined under <code>[common]</code> like this:</p>\n\n<span class=\"CodeExample-Title\">what you may need to change the ini file to</span>\n<pre><code class=\"ini\">\n[common]\nBaseline=20\n# etc.\n\nLine  0= !\"$%&'()*+,-.\nLine  1=/0123456789:;<\nLine  2=>?@ABCDEFGHIJKL\nLine  3=MNOPQRSTUVWXYZ\nLine  4=\\\\]^_abcdefghij\n# etc.\n\n0=6\n1=5\n2=7\n3=11\n4=11\n5=18\n# etc.\n</code></pre>\n\n\n\n\n<hr />\n\n<h3>Dynamically Changing Text Using <code>settext()</code></h3>\n\n<p>Here is a more complex example that display a randomly selected string out of a Lua table of possibilities.</p>\n\n<span class=\"CodeExample-Title\">Display one randomly selected string from a table:</span>\n<pre><code class=\"lua\">\n-- This \"phrases\" table contains a list of strings.  We're going to randomly\n-- select one and display it in the BitmapText actor below. In SM5, it is\n-- possible (and encouraged!) to create local variables that have scope over\n-- the current file, like this.\nlocal phrases = {\n   \"There's the boys.\",\n   \"Just move faster.\",\n   \"Worst in the world.\",\n   \"#bottomstruggles\"\n}\n\nreturn Def.ActorFrame{\n   InitCommand=function(self)\n      -- Remember that queuecommand() waits until the next tick of the engine's\n      -- game loop to execute whatever command we are queueing.\n      self:queuecommand(\"ChooseAPhrase\")\n   end,\n\n   ChooseAPhraseCommand=function(self)\n      -- When passed an integer, math.random() returns a random\n      -- integer that is no greater than the number you pass it,\n      -- but at least 1.  So, in this case, since #phrases = 4\n      -- i will be assigned to 1, 2, 3 or 4.\n      local i = math.random( #phrases )\n\n      -- In contrast, playcommand() will attempt to execute immediately.\n      -- playcommand() also allows us to pass a table of arguments to\n      -- the next command we want to run.  In this way, we can pass\n      -- variables that are otherwise local to this function of this\n      -- ActorFrame to the desired function of the desired BitmapText.\n      self:GetChild(\"CatchPhrase\"):playcommand(\"Update\", {PhraseIndex=i})\n   end,\n\n   LoadFont(\"Common normal\")..{\n      Name=\"CatchPhrase\",\n      InitCommand=cmd(diffuse, color(\"#FEDCBA\"); Center),\n      UpdateCommand=function(self, params)\n         -- The \"params\" argument comes in as a table with a single element,\n         -- \"PhraseIndex\"; use that index to get one of the string phrases from\n         -- the phrases variable and set this BitmapText to display that phrase.\n         self:settext( phrases[ params.PhraseIndex ] )\n      end\n   }\n}\n</code></pre>\n\n<p>The example above is admittedly rather artificial for the sake of demonstrating how to pass variables when issuing a <code>playcommand()</code> call.  The random index <code>i</code> could have just as easily been generated at the top of the file next to <code>local phrases</code> and not needed to be passed between the Actors&apos; functions.</p>\n\n<p>Still, it is good to know that this can be done.  Keeping variables local (to a file, to a function, etc.) is good practice to mitigate polluting the global Lua namespace with an abundance of single-use variables.  In this regard, it can be helpful to know as many ways to pass those local variables (between files, between functions, etc.) as possible.</p>","/Actors/LoadActor":"<h1>LoadActor()</h1>\n\n<p><em>LoadActor()</em> is a helper function with some extremely useful properties that make it a staple of any SM5 Lua scripting endeavor.</p>\n\n<h3>First Argument - A file path to Load</h3>\n\n<p>The function itself requires at least one argument, a string value of the file to load.  Let&apos;s consider a simple example, the code from <a data-component=\"Link\" href=\"/Actors/Sprite\">the page on Def.Sprite</a>, now rewritten to use <code>LoadActor()</code>:</p>\n\n<span class=\"CodeExample-Title\">A very simple LoadActor example:</span>\n<pre><code class=\"lua\">\n-- pass the function a path to an image file\n-- and append a table with the relevant commands\nLoadActor( \"WhatAreBirds.png\" )..{\n   InitCommand=function(self)\n      self:zoom(0.5):Center()\n   end\n}\n</code></pre>\n\n<p>As <a href=\"https://github.com/stepmania/stepmania/blob/a888506b3270d6c66d12cb2165fb8d4b1a7d978f/Themes/_fallback/Scripts/02%20ActorDef.lua#L95-L159\">its definition</a> in the _fallback theme demonstrates, <em>LoadActor()</em> can load Lua files, image/video files, sound files, models, and directories.  In this manner, it can effectively take the place of knowing when to use <code>Def.Sprite{}</code>, <code>Def.Sound{}</code>, <code>Def.Model{}</code> or <code>Def.Actor</code>.</p>\n\n<h3>Second Argument - A table to pass into the loaded file</h3>\n\n<p>The optional second argument of LoadActor() is where it really shines, however.  Let&apos;s look at this slightly more complex example which uses two Lua files.</p>\n\n<p><strong>Primary.lua</strong> will load <strong>Box.lua</strong> once for each available human player and pass in unique properties to each.  If only PLAYER_1 is available, only the red quadrilateral on the left will be drawn.  If only PLAYER_2 is available, only the blue quadrilateral on the right will be drawn.</p>\n\n<p><img alt=\"\" class=\"img-fluid\" src=\"/Lua-For-SM5/img/loadactor.png\" /></p>\n\n<p>This sort of setup allows us to keep generic code definitions in files like Box.lua, and load them as needed from the primary file with specific values passed in.</p>\n\n<span class=\"CodeExample-Title\">Primary.lua</span>\n<pre><code class=\"lua\">\nlocal af = Def.ActorFrame{\n   InitCommand=function(self) self:Center():sleep(9999) end\n}\n\nlocal box_values = {}\n\nbox_values.P1 = {\n   color = Color.Red,\n   x = _screen.cx-200,\n   y = _screen.cy,\n   h = 100,\n   w = 50\n}\n\nbox_values.P2 = {\n   color = Color.Blue,\n   x = _screen.cx+200,\n   y = _screen.cy,\n   h = 50,\n   w = 175\n}\n\n\n-- Loop through any available human players\n-- (as opposed to \"joined\" players, which can be misleading)\n-- and load a Box.lua to the ActorFrame for each.\nfor player in ivalues( GAMESTATE:GetHumanPlayers() ) do\n\n   -- Tranform a player enum string value into the part after\n   -- the underscore with the ToEnumShortString() helper function.\n\n   -- In this case \"PlayerNumber_P1\" becomes \"P1\"\n   -- and \"PlayerNumber_P2\" becomes \"P2\"\n   local pn = ToEnumShortString(player)\n\n   -- The contents of Box.lua return a Quad.  Keep reading!\n   -- Pass the specific box values we want into the file.\n   af[#af+1] = LoadActor( \"./Box.lua\", box_values[ pn ] )\nend\n\nreturn af\n</code></pre>\n\n<span class=\"CodeExample-Title\">Box.lua</span>\n<pre><code class=\"lua\">\n-- The box_values for this player from the primary lua file\n-- are brought into this file via the \"...\" syntax.\nlocal this_box = ...\n\nreturn Def.Quad{\n   InitCommand=function(self)\n      self:xy( this_box.x, this_box.y )\n      self:diffuse( this_box.color )\n      self:setsize( this_box.w, this_box.h  )\n   end\n}\n</code></pre>","/Actors/Model":"<h1>Model</h1>\n\n<p class=\"alert alert-info\"><em>This article was graciously contributed by <a href=\"https://github.com/JoseVarelaP\">JoseVarelaP</a>!</em></p>\n\n<p>\n\t<em>Model</em> is an actor class used to load 3D models into StepMania 5.  At this time, StepMania 5 only supports one format: <strong><em>MilkShape 3D ASCII</em></strong>.  This means that modeling must be done using the MilkShape 3D software which is only for Windows OS.\n</p>\n\n<p>Let&apos;s start by looking at a very simple example:</p>\n\n<span class=\"CodeExample-Title\">Simple Def.Model Example</span>\n<pre><code class=\"lua\">\nDef.Model {\n   Meshes=\"MyModel.txt\",\n   Materials=\"MyModel.txt\",\n   Bones=\"MyModel.txt\",\n   OnCommand=function(self)\n      self:Center()\n   end,\n}\n</code></pre>\n\n<p>\n\tThis example will load a plaintext file named <code>MyModel.txt</code> that was exported from MilkShape 3D in the <em>MilkShape 3D ASCII</em> format.  That single file will contain all the data needed to for a <code>Def.Model</code>, including the <code>Meshes</code>, <code>Materials</code>, and <code>Bones</code>.\n</p>\n\n\n<h3>Meshes, Materials, and Bones</h3>\n\n<p><strong>Meshes</strong> are the composition and structure of the <code>Model</code>.  This data represents the vertices that make the <code>Model</code> take shape.</p>\n\n<p><strong>Materials</strong> are the textures that the model will use. These can be any of the image formats listed in the <a data-component=\"Link\" href=\"/Introduction/Supported-File-Extensions\">Supported File Extensions</a> page. They can also be <em>.ini</em> files that define animated textures on a <a data-component=\"Link\" href=\"/Actors/Sprite\">Def.Sprite</a>.</p>\n\n<p><strong>Bones</strong> make the model come to life. They can be defined within the primary model file, or, in the case of dancing characters, be controlled via a separate file that only contains the bones.</p>\n\n<p>In the above example, all three attributes used the same filepath; all the necessary data was contained within a single file.  It is possible to configure the MilkShape 3D software to output distinct files for meshes, materials, and bones, and set each <code>Def.Model</code> attribute accordingly, but that is outside the scope of this lesson.</p>\n\n<p class=\"alert alert-warning\">All three attributes <strong>must</strong> be provided within <code>Def.Model</code> as paths to resources that can be loaded or StepMania will crash.</p>\n\n<p>If you wish to load the <code>Model</code> without having to add bones, use <a data-component=\"Link\" href=\"/Actors/LoadActor\">LoadActor()</a> instead.</p>\n\n<span class=\"CodeExample-Title\">Using LoadActor() to Make Life Easier</span>\n<pre><code class=\"lua\">LoadActor(\"MyModel.txt\")..{\n   OnCommand=function(self) self:Center() end\n}\n</code></pre>\n\n\n\n\n<h3>Dancing Character Models</h3>\n<p>While dancing characters typically appear in ScreenGameplay, it is possible to load them into any screen using <code>Def.Model</code>.  The following example can be used to load a randomly selected dancing character into the scene.</p>\n\n\n<span class=\"CodeExample-Title\">Random Dancing Character</span>\n<pre><code class=\"lua\">\n-- This will load a random character from SM5's ./Characters folder\n\n-- acquire a handle on a single, randomly selected\n-- dancing character using CHARMAN\nlocal CharacterToLoad = CHARMAN:GetRandomCharacter()\n\nDef.Model {\n   Meshes=CharacterToLoad:GetModelPath(),\n   Materials=CharacterToLoad:GetModelPath(),\n   Bones=CharacterToLoad:GetModelPath(),\n   OnCommand=function(self)\n      self:Center()\n   end,\n}\n</code></pre>\n\n<p><img alt=\"\" class=\"img-fluid\" src=\"/Lua-For-SM5/img/Model-Faraway.png\" /></p>\n\n<p>Wait a second - why is the model so far away?</p>\n\n\n<p>This is caused by how most 3D modelers have made their models to function with the corresponding <em>DDR-PC bones</em>, which are the most commonly used bones for dancing characters in StepMania.  As a result, dancing characters typically appear tiny when used outside of gameplay.</p>\n\n<p>Another thing to note is how the model is presented by default  it&apos;s facing away from us, the viewer.  This behavior can be changed by rotating on the <code>y</code> axis and explicitly setting a <code>z</code> value.</p>\n\n\n<span class=\"CodeExample-Title\">Random Dancing Character, Better</span>\n<pre><code class=\"lua\">-- acquire a handle on a single, randomly selected\n-- dancing character using CHARMAN\nlocal CharacterToLoad = CHARMAN:GetRandomCharacter()\n\nDef.Model {\n   Meshes=CharacterToLoad:GetModelPath(),\n   Materials=CharacterToLoad:GetModelPath(),\n   Bones=CharacterToLoad:GetModelPath(),\n   OnCommand=function(self)\n      -- Set the model's y rotation to accommodate our vantage point\n      -- and apply a z that will get the model closer to the camera.\n      self:Center():rotationy(180):z(300)\n   end,\n}\n</code></pre>\n\n<p><img alt=\"\" class=\"img-fluid\" src=\"/Lua-For-SM5/img/Model-MeshIntro.png\" /></p>\n\n\n<p>Okay, that&apos;s closer, but it still doens&apos;t look like the dancing character from gameplay!</p>\n\n<p>There&apos;s one additional \"gotcha\" regarding model animations in StepMania&apos;s dancing characters.  They are not handled by or defined within the model, but rather the <em>bones</em>.  To add animations to the model, pass the <code>Bones</code> attribute of the <code>Def.Model</code> a dedicated bones file.</p>\n\n\n<span class=\"CodeExample-Title\">Random Dancing Character, Fixed</span>\n<pre><code class=\"lua\">-- acquire a handle on a single, randomly selected\n-- dancing character using CHARMAN\nlocal CharacterToLoad = CHARMAN:GetRandomCharacter()\n\nDef.Model {\n   Meshes=CharacterToLoad:GetModelPath(),\n   Materials=CharacterToLoad:GetModelPath(),\n\n   -- For this example, we'll provide a path to a dedicated bones file\n   -- by using SM5's Character GetRestAnimationPath() method\n   -- for this example, we'll use the Rest Animation\n   Bones=CharacterToLoad:GetRestAnimationPath(),\n\n   OnCommand=function(self)\n      -- Set the model's y rotation to accommodate our vantage point\n      -- and apply a z that will get the model closer to the camera.\n      self:Center():rotationy(180):z(300)\n\n      -- since we're using the rest animation, we'll want to also set\n      -- the y() position to be lower on on the screen\n      self:addy(10)\n   end\n}\n</code></pre>\n\n\n<p><img alt=\"\" class=\"img-fluid\" src=\"/Lua-For-SM5/img/Model-WithBones.png\" /></p>\n\n\n<p>As a final note, some models may have inverted vertexes, which can cause some polygons to collide with one another and draw in the wrong order. To fix this, set the model&apos;s <code>Cull Mode</code> to <code>none</code>.</p>\n\n\n<span class=\"CodeExample-Title\">Random Dancing Character, CullMode None</span>\n<pre><code class=\"lua\">-- acquire a handle on a single, randomly selected\n-- dancing character using CHARMAN\nlocal CharacterToLoad = CHARMAN:GetRandomCharacter()\n\nDef.Model {\n   Meshes=CharacterToLoad:GetModelPath(),\n   Materials=CharacterToLoad:GetModelPath(),\n\n   Bones=CharacterToLoad:GetRestAnimationPath(),\n\n   OnCommand=function(self)\n      -- Set the model's y rotation to accommodate our vantage point\n      -- and apply a z that will get the model closer to the camera.\n      self:Center():rotationy(180):z(300)\n\n      -- since we're using the rest animation, we'll want to also set\n      -- the y() position to be lower on on the screen\n      self:addy(10)\n\n      -- finally, set the Cull Mode to none\n      self:cullmode(\"CullMode_None\")\n   end\n}\n</code></pre>","/Actors/Quad":"<h1>Quad</h1>\n\n<p><em>Quad</em> actors are programmatically drawn <em>quad</em>rilaterals that can have properties like size, position, and color.  If you don&apos;t specify, Quad actors are white and have a height and width of 0 by default.</p>\n\n<span class=\"CodeExample-Title\">A very simple Quad example:</span>\n<pre><code class=\"lua\">\n-- a white quad with height and width of 100px\nDef.Quad{\n   Name=\"WhiteQuad\",\n   InitCommand=function(self)\n      self:zoomto(100,100)\n   end\n}\n</code></pre>\n\n<p>A common question that people new to StepMania scripting have is:</p>\n\n<p><strong>Where are those commands like</strong> <code>zoomto()</code> <strong>coming from?</strong></p>\n\n<p>Keeping in mind that a <em>Quad</em> is a specific type of StepMania <em>Actor</em>, we can look to StepMania&apos;s Lua API for a complete list of methods available to all <a data-component=\"Link\" href=\"/LuaAPI#Actors-Actor\">Actor objects</a>.</p>\n\n<h2>A More Advanced Example</h2>\n\n<p>Since Quads are fairly simple, let&apos;s use another example to animate some Quads.  This example makes use of a new-to-SM5 feature, command-chaining, which is discussed more in-depth in the Command Chaining section.</p>\n\n<span class=\"CodeExample-Title\">Three Quads with animation:</span>\n<pre><code class=\"lua\">\n-- let's assume this file is being called via FGCHANGES from a simfile.\n-- Like in SM3.95, Actors from FGCHANGES that are not actively tweening\n-- are cleared from memory as the engine assumes they are \"done.\"\n-- To counteract this, we'll apply a sleep() tween to the parent ActorFrame\n\nreturn Def.ActorFrame{\n   -- the OnCommand here applies to the primary ActorFrame\n   OnCommand=function(self)\n      self:sleep(9999)\n   end,\n\n   -- a red quad that accelerates from offscreen-left to offscreen-right\n   -- this makes use of the _screen and Color aliases\n   Def.Quad{\n      Name=\"RedQuad\",\n      InitCommand=function(self)\n         self:zoomto(100,100):diffuse(Color.Red)\n      end,\n      OnCommand=function(self)\n         self:xy( -100, _screen.cy )\n             :accelerate( 2 ):x( _screen.w + 100 )\n      end\n   },\n\n   -- a blue quad that decelerates from offscreen-top to offscreen-bottom\n   -- this makes use of the xy() command, which is new to SM5\n   -- as well as command-chaining (read more on the next page!)\n   Def.Quad{\n      Name=\"BlueQuad\",\n      InitCommand=function(self)\n         self:zoomto( 100, 100 ):diffuse( Color.Blue )\n      end,\n      OnCommand=function(self)\n         self:xy( _screen.cx, -100)\n             :decelerate( 2 ):y( _screen.h + 100 )\n             :queuecommand( \"TriggerSpin\" )\n      end,\n      TriggerSpinCommand=function(self)\n         local greenquad_af = self:GetParent():GetChild( \"GreenQuadAF\" )\n         greenquad_af:GetChild( \"GreenQuad\" ):queuecommand( \"Grow\" )\n      end\n   },\n\n   -- a green quad that waits for the two quads above to finish tweening,\n   -- then grows out of the center of the screen while spinning\n\n   -- NOTE: We can't apply tween-based commands and actor effects like spin()\n   -- simultaneously.\n   -- zoomto() will override spin() for the duration of its linear tween.\n   -- The way to achive the effect of spinning toward the viewer is to\n   -- wrap the Quad in an ActorFrame, spin the ActorFrame, and zoom the Quad.\n   Def.ActorFrame{\n      Name=\"GreenQuadAF\",\n      InitCommand=function(self)\n         self:Center()\n             :spin():effectmagnitude(0,0,180)\n      end,\n\n      Def.Quad{\n         Name=\"GreenQuad\",\n         InitCommand=function(self)\n            self:zoomto(0,0):diffuse(Color.Green)\n         end,\n         GrowCommand=function(self)\n            self:linear(5):zoomto(_screen.w, _screen.h)\n         end,\n      }\n   }\n}\n</code></pre>\n\n<h2>The <em>_fallback</em> theme has some helpful aliases.</h2>\n\n<p>This example also uses a few helper tables defined in SM5&apos;s <em>_fallback</em> theme such as the <code>Color</code> table from <a href=\"https://github.com/stepmania/stepmania/blob/master/Themes/_fallback/Scripts/02%20Colors.lua\">02 Colors.lua</a>  and the <code>_screen</code> table from <a href=\"https://github.com/stepmania/stepmania/blob/master/Themes/_fallback/Scripts/01%20alias.lua\">01 alias.lua</a>.</p>","/Actors/Sound":"<h1>Sound</h1>\n\n<p>A <em>Sound</em> actor can be used to load and play sound files.  It supports panning between the left/right stereo channels and is intended for single-use sound effects.</p>\n\n<p class=\"alert alert-warning\">\nIf you need to play an audio file that you want to be cleanly <strong>looped</strong>, you&#8217;ll have better luck using the <code>SOUND:PlayMusicPart()</code> singleton method which is documented in <a data-component=\"Link\" href=\"/Singletons/SOUND\">SOUND</a>.\n</p>\n\n<span class=\"CodeExample-Title\">Minimal Example:</span>\n<pre><code class=\"lua\">\nDef.Sound{\n   -- Note that you must tell the sound actor to play()\n   File=\"filepath.ogg\",\n\n   OnCommand=function(self)\n      self:play()\n   end\n}\n</code></pre>\n\n<p><em>Sound</em> actors have three unique attributes: <code>SupportPan</code>, <code>SupportRateChanging</code>, and <code>IsAction</code></p>\n\n<ul>\n<li>\n\t<p>\n\t\t<strong>SupportPan</strong> Set this to <code>true</code> if you intend to have the sound played solely through the left (for <code>PLAYER_1</code>) or the right (for <code>PLAYER_2</code>) audio channel.  This is accomplished with the <code>playforplayer()</code> method.\n\t</p>\n</li>\n\n<li>\n\t<p>\n\t\t<strong>SupportRateChanging</strong> Set this to <code>true</code> if you intend to manipulate the pitch and/or speed of the underlying RageSound.\n\t</p>\n</li>\n<li>\n\t<p>\n\t\t<strong>IsAction</strong> Set this to <code>true</code> if you are want this Sound actor to be muted as a <em>theme action</em> via <kbd>F3</kbd>+<kbd>A</kbd>.  This can be useful to keep themers sane while they are repeatedly debugging a single screen over and over again.\n\t</p>\n</li>\n</ul>\n\n<span class=\"CodeExample-Title\">SupportPan Example:</span>\n<pre><code class=\"lua\">\n-- this Sound actor will play the current theme's\n-- \"common start\" sound first for PLAYER_1, then\n-- wait two seconds, and then play it for PLAYER_2\n\nDef.Sound{\n   File=THEME:GetPathS(\"common\", \"start\"),\n   Name=\"SFX_With_Pan\",\n\n   SupportPan=true,\n   SupportRateChanging=true,\n   IsAction=false,\n\n   OnCommand=function(self)\n      -- play the sound out of the left channel\n      self:playforplayer(PLAYER_1)\n      self:queuecommand(\"PlayAgain\")\n   end,\n   PlayAgainCommand=function(self)\n      self:sleep(2)\n\n      -- play the sound out of the right channel\n      self:playforplayer(PLAYER_2)\n   end\n}\n</code></pre>\n\n\n<span class=\"CodeExample-Title\">SupportRateChanging Example:</span>\n<pre><code class=\"lua\">\n-- variables local to this file\nlocal ragesound_file\nlocal current_pitch = 1\n\n-- this Sound actor will play the same audio file\n-- three times sequentially, each time with a higher pitch\nDef.Sound{\n   File=THEME:GetPathS(\"common\", \"start\"),\n   Name=\"SFX_With_Pitch\",\n   SupportRateChanging=true,\n\n   OnCommand=function(self)\n      self:queuecommand(\"Play\")\n   end,\n   PlayCommand=function(self)\n\n      -- get the underlying sound file data\n      ragesound_file = self:get()\n\n      -- RageSound:pitch() take a float where\n      -- 1.0 is \"normal\", 2.0 is twice as high as normal, etc.\n      ragesound_file:pitch( current_pitch )\n\n      -- play the sound file using both stereo channels\n      self:play()\n\n      -- increment current_pitch\n      current_pitch = current_pitch + 1\n\n      -- prevent infinite looping\n      if current_pitch < 4 then\n         -- sleep for two seconds and do it again\n         self:sleep(2):queuecommand(\"Play\")\n      end\n   end\n}\n</code></pre>","/Actors/Sprite":"<h1>Sprite</h1>\n\n<p>\n   <em>Sprite</em> actors can be used to load, display, and manipulate image and video data in StepMania 5. If you want to load a png graphic and move it around, or animate a character from a sprite sheet, or play a video file, then the <em>Sprite</em> actor is what you're looking for.\n</p>\n\n<p>\n   See the page on <a data-component=\"Link\" href=\"/Introduction/Supported-File-Extensions\">Supported File Extensions</a> for a complete list of which filetypes are supported.\n</p>\n\n<p>\n   As Lua tables, Sprite actors have one unique attribute that is worth knowing about, <code>Texture</code>. Let's start with a very simple example of Sprite usage.\n</p>\n\n\n<span class=\"CodeExample-Title\">A very simple Sprite example:</span>\n<pre><code class=\"lua\">\nDef.Sprite{\n   Texture=\"WhatAreBirds.png\",\n   InitCommand=function(self)\n      self:zoom(0.5):Center()\n   end\n}\n</code></pre>\n\n<p>\n   This example will load a file titled <em>WhatAreBirds.png</em> in the same directory as the current Lua file, <code>Center()</code> it within its parent ActorFrame, and apply a 50% zoom (make it half as large).\n</p>\n\n<p>\n   A common question that people new to StepMania scripting have is:\n</p>\n\n<p>\n   <strong>Where are those commands like</strong> <code>zoom()</code> <strong>and</strong> <code>Center()</code> <strong>coming from?</strong>\n</p>\n\n<p>\n   Keeping in mind that a <em>Sprite</em> is a specific type of StepMania <em>Actor</em>, we can look to StepMania's Lua API for a complete list of methods available first to all <a data-component=\"Link\" href=\"/LuaAPI#Actors-Actor\">Actor</a> and then to <a data-component=\"Link\" href=\"/LuaAPI#Actors-Sprite\">Sprite</a> specifically.\n</p>\n\n\n<h2>Animated Spritesheets</h2>\n<p>\n   What about sprite sheets for animated sprites? StepMania's engine is hardcoded to look for patterns in the filename with Sprite actors. If you are loading a sprite sheet that you intend to animate through, you'll want to ensure that the filename of the png you are loading ends with the pattern: <em>(columns)</em>x<em>(rows)</em>\n</p>\n\n<p>\n   For example, this sprite of Dr. Wily has 6 columns across 1 row.\n</p>\n\n<p>\n   <img alt=\"a 6 by 1 spritesheet of Dr. Wily\" src=\"/Lua-For-SM5/img/wily%206x1.png\">\n</p>\n\n<p>\n   For StepMania to recognize it properly as a sprite sheet, you would want to append <strong>6x1</strong> to the end of the filename, resulting in something like <strong>Wily 6x1.png</strong>.\n</p>\n\n<p>\n   When working with sprite sheets, <em>Sprite</em> actors can have infinitely many more attributes for each <code>Frame</code> and each Frame's <code>Delay</code> values, but there are better ways of doing handling those. Let's look at an old example from <a href=\"https://www.youtube.com/watch?v=KhwxI60WeWU\">The Ballad of Ian</a> from the D.O.W.N.S. 3 ITG tournament in January 2013.\n</p>\n\n<span class=\"CodeExample-Title\">Old example from The Ballad of Ian. There's a better way!</span>\n<pre><code class=\"lua\">\nDef.Sprite{\n   Texture=\"spin 13x1.png\",\n   Frame0000=0,\n   Delay0000=0.0516,\n   Frame0001=1,\n   Delay0001=0.0516,\n   Frame0002=2,\n   Delay0002=0.0516,\n   Frame0003=3,\n   Delay0003=0.0516,\n   Frame0004=4,\n   Delay0004=0.0516,\n   Frame0005=5,\n   Delay0005=0.0516,\n   Frame0006=6,\n   Delay0006=0.0516,\n   Frame0007=7,\n   Delay0007=0.0516,\n   Frame0008=8,\n   Delay0008=0.0516,\n   Frame0009=9,\n   Delay0009=0.0516,\n   Frame0010=10,\n   Delay0010=0.0516,\n   Frame0011=11,\n   Delay0011=0.0516,\n   Frame0012=12,\n   Delay0012=0.0516,\n   InitCommand=cmd( diffusealpha,0.5; zoom,0.6; vertalign,bottom; Center),\n   OnCommand=cmd( sleep,8; linear,1; diffusealpha,0 ),\n}\n</code></pre>\n\n<p>\n   While it is possible to specify frames and frame durations (delays) like this, you'll probably get the idea that this style is repetitive and prone to typos. Here's a much more condensed version that accomplishes the same thing.\n</p>\n\n\n<span class=\"CodeExample-Title\">A better way to manage sprite sheets:</span>\n<pre><code class=\"lua\">\nDef.Sprite {\n   Texture=\"spin 13x1.png\",\n   InitCommand=function(self)\n      self:diffusealpha(0.5):zoom(0.6):vertalign(bottom):Center()\n      self:SetAllStateDelays(0.0516)\n   end,\n   OnCommand=function(self)\n      self:sleep(8):linear(1):diffusealpha(0)\n   end\n}\n</code></pre>\n\n<p>\n   Since the the time between each frame of animation is set globally using the <code>SetAllStateDelays()</code> method, the code is overall less prone to mistakes via typos.\n</p>\n\n<p>\n   It is worth noting that in the above, animated examples, StepMania's engine will start with the first frame and progress to the last one in order. There are times when we may want to only animate through a portion of a sprite sheet at any given moment, like with this character sprite sheet from a typical 16bit JRPG:\n</p>\n\n<p>\n   <img alt=\"a 4 by 4 spritesheet of SteveReen\" src=\"/Lua-For-SM5/img/Reen%204x4.png\">\n</p>\n\n<p>\n   If we want the <em>Sprite</em> actor to appear to be facing downward, then we'd only want StepMania to be animating through the first row of the sprite sheet. Here's a way to handle that.\n</p>\n\n\n<span class=\"CodeExample-Title\">A complex Sprite example:</span>\n<pre><code class=\"lua\">\n-- duration between frames, in seconds\nlocal duration_between_frames = 0.15\n\n-- We want SM5 to think of this as a single Sprite actor that sometimes\n-- displays frames 0-3, and other times displays frames 8-11, so we're\n-- going to need to manually specify each frame index like this.\n\n-- The sequence of frames here corresponds with the physical layout of\n-- the \"guy 4x4.png\" sprite sheet image shown above.\n\n-- Yes, this \"frames\" table could be programmatically generated with\n-- a nested loop but I'm explicitly writing it out here so it's\n-- easier to see what it contains for this example.\n\nlocal frames = {\n   Down = {\n      { Frame=0,  Delay=duration_between_frames},\n      { Frame=1,  Delay=duration_between_frames},\n      { Frame=2,  Delay=duration_between_frames},\n      { Frame=3,  Delay=duration_between_frames}\n   },\n   Left = {\n      { Frame=4,  Delay=duration_between_frames},\n      { Frame=5,  Delay=duration_between_frames},\n      { Frame=6,  Delay=duration_between_frames},\n      { Frame=7,  Delay=duration_between_frames}\n   },\n   Right = {\n      { Frame=8,  Delay=duration_between_frames},\n      { Frame=9,  Delay=duration_between_frames},\n      { Frame=10, Delay=duration_between_frames},\n      { Frame=11, Delay=duration_between_frames}\n   },\n   Up = {\n      { Frame=12, Delay=duration_between_frames},\n      { Frame=13, Delay=duration_between_frames},\n      { Frame=14, Delay=duration_between_frames},\n      { Frame=15, Delay=duration_between_frames}\n   }\n}\n\n\nlocal af = Def.ActorFrame{\n   OnCommand=function(self) self:sleep(999):Center() end\n}\n\naf[#af+1] = Def.Sprite{\n   Texture=\"Reen 4x4.png\",\n\n   InitCommand=function(self)\n      -- when not actively tweening across the screen, the Sprite should\n      -- not animate, so as to appear to be \"standing\" in place\n      self:animate(false)\n\n      -- align to left and v-middle\n      self:halign(0):valign(0.5)\n      self:Center()\n\n      -- initialize the sprite state so that the sprite\n      -- starts by facing downward\n      self:SetStateProperties( frames.Down )\n   end,\n\n   -- In SM5, the StepMessage is broadcast whenever either player\n   -- steps on a game button during gameplay.\n   -- This doesn't handle buttons like START or SELECT.\n   StepMessageCommand=function(self, params)\n\n      -- As an arbitrary decision for the sake of this example\n      -- let's only pay attention to steps from PLAYER_1\n      if params.PlayerNumber == PLAYER_1 then\n\n         -- turn animation back on for a brief moment\n         self:animate(true)\n\n         -- people can hit buttons faster than every 0.25 seconds, so it's\n         -- possible to queue so many tweens that SM5 complains about a tween\n         -- overflow. Mitigate this by canceling the previous linear tween, if\n         -- one is still running, via the stoptweening() command.  Then\n         -- apply a new linear tween to move the sprite one directional unit.\n         self:stoptweening():linear(0.25)\n\n         -- the \"Column\" param will match up with a Gameplay arrow column\n         -- dance has 4 columns, pump has 5, etc.\n         -- Column is 0-indexed because it's coming from C++.\n\n         -- LEFT\n         if params.Column == 0 then\n            self:SetStateProperties( frames.Left )\n            self:x(self:GetX() + (-1 * self:GetWidth()))\n\n         -- DOWN\n         elseif params.Column == 1 then\n            self:SetStateProperties( frames.Down )\n            self:y(self:GetY() + self:GetWidth())\n\n         -- UP\n         elseif params.Column == 2 then\n            self:SetStateProperties( frames.Up )\n            self:y(self:GetY() + (-1 * self:GetWidth()))\n\n         -- RIGHT\n         elseif params.Column == 3 then\n            self:SetStateProperties( frames.Right )\n            self:x(self:GetX() + self:GetWidth() )\n\n         end\n\n         -- The Sprite has tweened, so queue a command to stop the\n         -- animation. We can't call animate(false) in here because\n         -- it will terminate the call to animate(true) early and the\n         -- sprite will never animate.\n         self:queuecommand(\"StopAnimating\")\n      end\n   end,\n\n   StopAnimatingCommand=function(self)\n      -- stop animating and set the sprite's frame to 0,\n      -- regardless of direction the sprite is \"facing\".\n      self:animate(false):setstate(0)\n   end\n}\n\nreturn af\n</code></pre>\n","/Best-Practices/Command-Chaining":"<h1>Command-Chaining</h1>\n\n<p>In StepMania 5, commands applied to actors can be chained, resulting in a more clean and terse syntax than was possible before.</p>\n\n<p>The following two syntaxes produce the same results:</p>\n\n<span class=\"CodeExample-Title\">Long Form</span>\n<pre><code class=\"lua\">\nDef.Quad{\n   OnCommand=function(self)\n      self:zoomto(100,200)\n      self:xy(_screen.cx, 100)\n      self:diffuse(Color.Green)\n      self:linear(1)\n      self:y(_screen.h-100)\n   end\n}\n</code></pre>\n\n<span class=\"CodeExample-Title\">Condensed Via Command Chaining</span>\n<pre><code class=\"lua\">\nDef.Quad{\n   OnCommand=function(self)\n      self:zoomto(100,200):xy(_screen.cx, 100):diffuse(Color.Green)\n          :linear(1):y(_screen.h-100)\n   end\n}\n</code></pre>\n\n<p>While commands <em>can</em> be chained ad infinitum, an appropriate rule of thumb is to chain contextually-related commands together, and start a new line when a new context arises.  For example, consider starting with a tween command and then successively chaining the commands that are to be tweened.</p>","/Best-Practices/Debugging":"<h1>Debugging</h1>\n\n<p>Using <a data-component=\"Link\" href=\"/Singletons/SCREENMAN\">SCREENMAN</a>&apos;s <code>SystemMessage()</code> method may perhaps be the most tried-and-true means of quickly displaying debugging output to the Screen.  <code>SystemMessage()</code> accepts a string as an argument and displays it at the top of the screen for a few seconds.</p>\n\n<p>Let&apos;s jump into a simple example from ScreenGameplay where we listen for and print out JudgmentMessages.  A <em>Judgment</em> is how each step is evaluated as it passes in Gameplay.  Each Judgment has a corresponding JudgmentMessage broadcast by the engine that contains information about that Judgment.</p>\n\n<span class=\"CodeExample-Title\">Using SystemMessage() to print debug output</span>\n<pre><code class=\"lua\">\n-- Since the engine only broadcasts JudgmentMessages to ScreenGameplay,\n-- this example only makes sense and does anything in ScreenGameplay.\n\nreturn Def.Actor{\n   JudgmentMessageCommand=function(self, params)\n      -- Note that JudgementMessages will be broadcast for any human players,\n      -- but to keep this example simple, we'll limit it to PLAYER_1.\n      if params.Player == PLAYER_1 then\n\n         -- This SystemMessage will display each judgment in string\n         -- form at the top of the screen as it occurs.\n         -- So, if a note passes and the player misses it,\n         -- \"TapNoteScore_Miss\"  would be displayed at the\n         -- top of the screen.\n         --\n         -- A W1 judgment (Marvelous in DDR, Fantastic in ITG, etc.)\n         -- would display \"TapNoteScore_W1\".\n         -- (Note that the \"W\" is for window, as in \"timing window.\")\n         --\n         -- Hold notes have their own, separate judgment system.\n         -- So, when a hold note is judged, a JudgmentMessage will be\n         -- broadcast, but the TapNoteScore parameter will be nil.\n         -- Account for that here with a logical or statement that tries\n         -- params.HoldNoteScore if params.TapNoteScore is nil.\n         SCREENMAN:SystemMessage( params.TapNoteScore or params.HoldNoteScore )\n      end\n   end\n}\n</code></pre>\n\n<h3>Using SystemMessage() to display Table output</h3>\n\n<p><code>SystemMessage()</code> displays strings, not tables, so if in our debugging endeavors we want such functionality, we&apos;ll have to enhance SystemMessage some with custom Lua.  The <em>Simply Love</em> theme for SM5 <a href=\"https://github.com/quietly-turning/Simply-Love-SM5/blob/master/Scripts/06%20SL-Utilities.lua\">includes some helper functions</a>.  As the author of that theme, I encourage you to use that code in your own scripting/theming endeavors.  One way to do this is to include a copy of <strong>SL-Utilities.lua</strong> in the <em>./Scripts</em> directory of your current theme.</p>\n\n<p>The function defined in <strong>SL-Utilities.lua</strong> that is relevant here is <code>SM()</code>  which is short for SystemMessage.  Assuming that SL-Utilities.lua is copied into the current theme&apos;s Scripts directory and loaded (by restarting StepMania or pressing <kbd>Control</kbd> <kbd>F2</kbd>), this example will print table of the which steps were just judged in a JudgmentMessage.</p>\n\n<span class=\"CodeExample-Title\">Using SM() to display a small Lua table</span>\n<pre><code class=\"lua\">\nreturn Def.Actor{\n   JudgmentMessageCommand=function(self, params)\n      -- Again, limit to  PLAYER_1 for a more simple example.\n      if params.Player == PLAYER_1 then\n         -- SystemMessage a stringified table of note columns\n         -- as each judgment occurs\n         SM( params.Notes )\n      end\n   end\n}\n</code></pre>\n\n<p>The screenshot below shows that columns 4 and 1 (a left-right jump) were just missed.</p>\n\n<p><img src=\"/Lua-For-SM5/img/using-SM-to-debug-table.png\" alt=\"a screenshot demonstrating how to use the SM helper function to debug a Lua table\"/></p>\n\n<h3>Knowing when to use Trace()</h3>\n\n<p>Please note that <code>SystemMessage()</code> is not the only debugging tool available in StepMania.  SystemMessage() actually has a rather limited usefulness because large Lua tables can easily contain more data than StepMania&apos;s window can reasonably display.  In such situations, a proper Lua <code>Trace()</code> is preferred.  Output from <code>Trace()</code> is written to <em>Logs/Log.txt</em></p>\n\n<p>StepMania&apos;s <em>_fallback</em> theme includes a  <code>rec_print_table()</code> function to assist with recursively printing deeply nested Lua tables to the Log.txt file.  Here is the example from above, reworked to to use <code>rec_print_table()</code> to write the entire params table to file.</p>\n\n<span class=\"CodeExample-Title\">Using rec_print_table() to log a large Lua table</span>\n<pre><code class=\"lua\">\nreturn Def.Actor{\n   JudgmentMessageCommand=function(self, params)\n      -- recursive print the entire table of JudgmentMessage parameters\n      -- to Logs/Log.txt.  This would be more information than could fit\n      -- onscreen at a single moment.\n      rec_print_table( params )\n   end\n}\n</code></pre>","/Examples/Arbitrary-Input":"<div>\n\n<h1>Handling Arbitrary Input</h1>\n\n<p>Arbitrary input (from game buttons, menu buttons, coin buttons, etc.) can be detected and handled by using the <code>AddInputCallback()</code> method of any screen.</p>\n\n<span class=\"CodeExample-Title\">A very simple InputCallback example:</span>\n<pre><code class=\"lua\">\nlocal function InputHandler( event )\n   -- do stuff with the event table in here\n   -- the attributes of the event table are documented below\nend\n\nDef.ActorFrame{\n   OnCommand=function(self)\n      SCREENMAN:GetTopScreen():AddInputCallback( InputHandler )\n   end,\n}\n</code></pre>\n\n<p>This adds the lua function <code>InputHandler()</code> to the list of functions the screen will pass input to.  Whenever an input event occurs, <code>InputHandler()</code> will be passed a table with the details of the event.</p>\n\n<p class=\"alert alert-warning\">\nNote that this method should <strong>not</strong> be used to handle text input from the user because it would not handle localization or different keyboard layouts.\n</p>\n\n<p>The screen and your own callback function will both be passed input events, so be aware of what input the current screen responds to and consider the effects.</p>\n\n<p>Details of the table containing the event data is as follows.  In the very simple example above, this table would be the <code>event</code> table that is passed into the <code>InputHandler()</code> function on <em>every</em> input event.</p>\n\n<span class=\"CodeExample-Title\">An input event table:</span>\n<pre><code class=\"lua\">\n{\n   -- The semi-raw button that was pressed.\n   -- This is what the button was mapped to by\n   -- the keymap settings, but without the\n   -- conversions that occur when\n   -- OnlyDedicatedMenuButtons is true.\n   -- Will be empty if the button was not mapped.\n   button = string,\n\n   -- The type of event.  One of three possible values:\n   --\t\t\"InputEventType_FirstPress\"\n   --\t\t\"InputEventType_Repeat\"\n   --\t\t\"InputEventType_Release\".\n   type = string,\n\n   -- The cooked button that was pressed.\n   -- This is button with mapping that occurs when\n   -- OnlyDedicatedMenuButtons is true applied.\n   -- This is nil for unmapped buttons.\n   GameButton = string,\n\n   -- The player that the controller is mapped to, or nil.\n   -- \"PlayerNumber_P1\" or \"PlayerNumber_P2\"\n   -- or nil if the event wasn't mapped to either player.\n   PlayerNumber = PlayerNumber,\n\n   -- The game controller this event was mapped to.\n   -- \"GameController_1\" or \"GameController_2\"\n   -- or nil if the event wasn't mapped to either controller.\n   controller = string,\n\n   -- The raw details of the event.\n   -- Most of the time, this will be \"lower level\"\n   -- than anything you might want themeside.\n   DeviceInput = {\n\n      -- The type of device.  The first half of the string will be \"Device_\",\n      -- the second half will be from InputDeviceNames in RageInputDevice.cpp.\n      device = string,\n\n      -- The button that was pressed. the first half of the string will\n      -- be \"DeviceButton_\", the second half will be from InitNames\n      -- in RageInputDevice.cpp.\n      button= string,\n\n      -- A floating point value for analog input, (ie. mouse)\n      level = float,\n\n      -- Mousewheel input.\n      z = float,\n\n      -- Whether the button is down.\n      -- This is level with a threshold and debouncing applied.\n      down = bool,\n\n      -- How long ago this input occurred, in seconds.\n      ago = float,\n\n      -- True if the device is a joystick.\n      is_joystick = bool,\n\n      -- True if the device is a mouse.\n      is_mouse = bool\n   },\n\n   -- Unknown purpose.  Probably related to Netplay.\n   MultiPlayer = string,\n}\n</code></pre>\n\n\n\n\n<p>Here is a well-commented example, courtesy of Kyzentun.   It consists of a few variables, an input handling function, and an ActorFrame with two BitmapText actors whose text will be set by the input handling function.</p>\n\n<p>Note that this example only displays information from the <em>most recent</em> button press because new input events (first_press, repeat, release) are triggered on a per-button basis.   It is possible to maintain a list of which buttons are being held in a Lua table, but this is left as an exercise for the reader for now. :)</p>\n\n<span class=\"CodeExample-Title\">A more practical Input Callback example:</span>\n<pre><code class=\"lua\">\n-- button_text and game_button_text will be set to actors when those actors\n-- are created. These local variables exist to skip the hassle of calling\n-- GetChild().\nlocal button_text = false\nlocal game_button_text = false\n\n-- button_text will be used to show the button field from the input event,\n-- and game_button_text will be used for the GameButton field.\tThese two\n-- fields can be different because Stepmania has the OnlyDedicatedMenuButtons\n-- preference.\tThat preference is intended to make it so that only the menu\n-- buttons (MenuLeft, MenuRight, MenuUp, MenuDown, commonly only MenuLeft and\n-- MenuRight on cabinets) can be used in menus.\t If OnlyDedicatedMenuButtons\n-- is false, then Left will be translated to MenuLeft, and the GameButton\n-- field will be set to MenuLeft when Left on the pad is pressed.\n-- This digression about menu button translation really only matters to\n-- themers trying to make a good menu control scheme.\tFor a simfile played\n-- on a cabinet, GameButton will usually be nil, because most cabinets are\n-- set not to map the pads to the menu buttons.\n\n-- Since both text actors will have their text formatted the same way, it's\n-- useful to make a wrapper function to avoid duplicating the formatting.\n-- When the formatting changes, say changing spaces to colons, only\n-- set_button_text needs to change.\nlocal function set_button_text(text_actor, pn, button, press_type)\n   text_actor:settext( ToEnumShortString(pn) .. \" \" .. button .. \" \" .. press_type )\nend\n\n-- Lua.xml lists all the parts of the event table.\nlocal function InputHandler( event )\n   -- If a button that is not mapped to any player is pressed, the\n   -- PlayerNumber field will be nil.\tOtherwise, it will be PLAYER_1 or\n   -- PLAYER_2.\n   if not event.PlayerNumber then return end\n   -- The delay between FirstPress and Repeat is set by the RepeatDelay\n   -- metric for the screen.\n   -- The number of repeats per second is set by the RepeatRate metric.\n   local press_type= ToEnumShortString(event.type)\n   set_button_text(button_text, event.PlayerNumber, event.button, press_type)\n   set_button_text(game_button_text, event.PlayerNumber, event.GameButton, press_type)\nend\n\nreturn Def.ActorFrame{\n   -- We add a sleeper actor so this can be used in a BGCHANGE lua\n   -- file and the file won't be unloaded.\tThis isn't necessary when\n   -- making a screen in a theme.\n   Def.Actor{\n      InitCommand=function(self)\n         self:hibernate(1000)\n      end\n   },\n\n\n   -- AddInputCallback cannot be in an InitCommand. InitCommands happen\n   -- before the screen is the top screen, so calling GetTopScreen during an\n   -- InitCommand would fetch the screen being transitioned out of that\n   -- is about to be destroyed.\n   OnCommand=function(self)\n      SCREENMAN:GetTopScreen():AddInputCallback( InputHandler )\n   end,\n\n   Def.BitmapText{\n   Font=\"Common Normal\",\n   InitCommand=function(self)\n\n   -- Since button_text is set inside an InitCommand, it occurs before the\n   -- OnCommand that adds the input callback, so the input callback\n   -- doesn't need to worry about whether button_text is initialized.\n   button_text = self\n\n   -- Lua is whitespace insensitive, so sticking a line break before a\n   -- colon in the middle of a chain of functions is valid.\n   self:xy(_screen.cx, _screen.cy-12)\n   \t-- Colors are just tables of four numbers. Using html\n   \t-- strings and the color function would also work.\n   \t:DiffuseAndStroke({.75, .75, 0, 1}, {0, 0, 0, 1})\n   end\n   },\n\nDef.BitmapText{\n   Font=\"Common Normal\",\n   InitCommand= function(self)\n      game_button_text = self\n      self:xy(_screen.cx, _screen.cy+12)\n      \t:DiffuseAndStroke({.75, 0, .75, 1}, {0, 0, 0, 1})\n      end\n   },\n}\n</code></pre>\n\n</div>\n","/Examples/Simple-Tweens":"<div>\n   <h1>\n      Simple Tweens\n   </h1>\n   <h2>\n      Animate things from one state to another\n   </h2>\n   <p>\n      A <em>tween</em> is a process in computer animation in which an object is manipulated (translated, transformed, altered, etc.) from a starting state to an ending state. In the context of StepMania scripting, tweens are used to visually animate Actors.\n   </p>\n   <p>\n      Using StepMania's Lua API, you can use tweens for many tasks, including (but not limited to):\n   </p>\n   <ul>\n      <li>make a <a data-component=\"Link\" href=\"/Actors/Sprite\">Sprite Actor</a> grow to be twice as large by tweening <code>zoom(2)</code>\n      </li>\n      <li>make the entire Screen Actor appear to fade out by tweening <code>diffuse(0,0,0,0)</code>\n      </li>\n      <li>gradually make a <a data-component=\"Link\" href=\"/Actors/BitmapText\">BitmapText Actor</a> by tweening <code>diffusealpha(0)</code>\n      </li>\n      <li>move a <a data-component=\"Link\" href=\"/Actors/Quad\">Quad Actor</a> diagonally 100 pixels down and 100 pixels right by tweening <code>xy(100,100)</code>\n      </li>\n      <li>etc.\n      </li>\n   </ul>\n   <p>\n      This tutorial lists the types of simple tweens available through the <a data-component=\"Link\" href=\"/LuaAPI\">Lua API</a>, discusses how to use tweens, and concludes with a full example.\n   </p>\n   <p>\n      To learn more about Actors and Lua scripting in StepMania, check out the <a data-component=\"Link\" href=\"/Introduction/Foreword\">What Are Actors?</a> tutorial. To learn more about what Actor methods are available for tweening, refer to the <a data-component=\"Link\" href=\"/LuaAPI#Actors-Actor\">Actor subsection</a> of the Lua API.\n   </p>\n   <h2>\n      Tweens Defined by the Engine\n   </h2>\n   <p>\n      The StepMania engine defines four simple tweens directly: <em>linear</em>, <em>accelerate</em>, <em>decelerate</em>, and <em>spring</em>.\n   </p>\n   <ul>\n      <li>\n         <code>linear()</code> - Execute following commands steadily, at a constant rate.\n      </li>\n      <li>\n         <code>accelerate()</code> - Starts slow and progressively speeds up.\n      </li>\n      <li>\n         <code>decelerate()</code> - Starts fast and progressively slows down.\n      </li>\n      <li>\n         <code>spring()</code> - Rapidly shoots beyond the desired end state, then springs back into place.\n      </li>\n   </ul>\n   <p>\n      For completion's sake, it is worth noting here that <code>sleep()</code> is also a tween, even though most Lua scripters working with StepMania don't think of it as such. <code>sleep()</code> will wait for the specified duration, then execute all following commands at once.\n   </p>\n   <h2>\n      Tweens Defined by the <em>_fallback</em> theme\n   </h2>\n   <p>\n      The engine also defines a fifth tween type, <em>bezier</em>, which allows scripters to custom define more complex tweens. Indeed, the <em>_fallback</em> theme uses the <code>bezier()</code> tween type to predefine a few extra tweens for us that are simple to use.\n   </p>\n   <ul>\n      <li>\n         <code>smooth()</code> - Slow to start, fast in the middle, slow to finish.\n      </li>\n      <li>\n         <code>bouncebegin()</code> - Briefly inverts the tween at first, giving the appearance of bouncing to start.\n      </li>\n      <li>\n         <code>bounceend()</code> - Briefly inverts the tween at the end, giving the appearance of bouncing to end.\n      </li>\n      <li>\n         <code>drop()</code> - Slows as it approaches its end state, then briefly accelerates the final few frames.\n      </li>\n   </ul>\n   <p>\n      If you are interested in learning more about <code>bezier()</code> tweens, you can inspect <strong><a href=\"https://github.com/stepmania/stepmania/blob/master/Themes/_fallback/Scripts/02%20Actor.lua\">02 Actor.lua</a></strong> in the _fallback theme's Scripts directory to see how <em>smooth</em>, <em>bouncebegin</em>, <em>bounceend</em>, and <em>drop</em> are defined in Lua.\n   </p>\n   <h2>\n      How to Use Tweens\n   </h2>\n   <p>\n      Knowing <em>what</em> we can use is great, but it's only half the picture. If you apply a tween to an Actor without any further commands, it won't <em>do</em> anything. Tweens need additional commands to execute over the course of their duration in order to animate in any meaningful way.\n   </p>\n   <p>\n      Each of the tweens listed above takes a single argument: a <em>number</em> representing a duration in seconds for long the StepMania engine should tween the methods that immediately follow for.\n   </p>\n   <p>\n      Let's illustrate what we mean by this with some examples.\n   </p><span class=\"CodeExample-Title\">Tween a quad to become twice as large:</span>\n   <pre><code class=\"lua\">\nDef.Quad{\n   OnCommand=function(self)\n      -- draw a Quad in center of the screen, make it 100x100 pixels,\n      -- and make it red\n      self:Center():zoomto(100,100):diffuse(1,0,0,1)\n\n      -- over a duration of 3 seconds, have the\n      -- quad zoom to be twice its initial size\n      self:linear(3):zoom(2)\n   end\n}\n</code></pre>\n   <p>\n      The next example is somewhat more fun in that it appears to spin the entire screen around, but it also illustrates a gotcha with tweening that you should be aware of.\n   </p><span class=\"CodeExample-Title\">Rotate the entire Screen on the z-axis:</span>\n   <pre><code class=\"lua\">\nDef.Actor{\n   OnCommand=function(self)\n      -- get the current Screen object using SCREENMAN\n      local screen = SCREENMAN:GetTopScreen()\n\n      -- over a duration of 2 seconds, have\n      -- the entire screen rotate 360 degrees\n      -- clockwise on the z-axis\n      screen:accelerate(2):rotationz(360)\n\n      -- wait one second\n      screen:sleep(1)\n\n      -- This should spin the screen around again, right?\n      -- Not quite. The screen's z-rotation is already set\n      -- to 360, so this will have no visible effect.\n      screen:accelerate(2):rotationz(360)\n\n      -- This, however, will because it adds more rotation\n      -- to the current state, rather than the initial state.\n      screen:accelerate(2):addrotationz(360)\n   end\n}\n</code></pre>\n   <p>\n      Can you tween more than one property of an actor at once? Absolutely.\n   </p>\n   <p>\n      The next example demonstrates that it is possible to tween multiple methods with a single tween. After calling a tween method, all methods following it will be tweened until the next tween method is encountered.\n   </p><span class=\"CodeExample-Title\">Move a quad across the screen while fading it out:</span>\n   <pre><code class=\"lua\">\nDef.Quad{\n   OnCommand=function(self)\n      -- draw a Quad at the top-left of the screen, which is the\n      -- default draw position if no x or y coordinates are specified,\n      -- and make it 100x100 pixels\n      self:zoomto(100,100)\n\n      -- over a duration of 2 seconds, have the\n      -- quad move to the bottom-right of the screen,\n      -- and fade out by tweening the alpha channel to 0\n      -- This comprises one full tween.\n      self:decelerate(2):xy( _screen.w, _screen.h ):diffusealpha(0)\n\n      -- Here is a second, unique tween that tweens three methods\n      -- to change the color, xy-position, and y-rotation of the Quad.\n      -- Note that line breaks are fine.\n      self:accelerate(3)\n          :diffuse(1,0,0,1)\n          :xy( _screen.cx, _screen.cy )\n          :addrotationy(1080)\n   end\n}\n</code></pre>\n   <h2>\n      Tweens in Motion\n   </h2>\n   <p>\n      Reading about tweens good, but a visual is worth a thousand words. Here is a scripted simfile you can run in StepMania 5 that briefly demonstrates each of these tweens, one after another.\n   </p>\n   <p>\n      You can download that here! <a href=\"/Lua-For-SM5/downloads/Simple-Tweens.zip\">Simple-Tweens.zip</a>\n   </p>\n</div>\n","/Singletons/SCREENMAN":"<h1>SCREENMAN Singleton</h1>\n\n<p>The SCREENMAN (short for <em>Screen Manager</em>) singleton is primarily used in conjunction with its <code>GetTopScreen()</code> method to do just that  get the screen that is currently on the top of StepMania&apos;s screen stack.  (This typically means the current screen.)</p>\n\n<p>SCREENMAN also has a some other methods which are worth knowing, namely <code>SystemMessage()</code>.   As we&apos;ll demonstrate in <a data-component=\"Link\" href=\"/Best-Practices/Debugging\">the chapter on Debugging</a>, <code>SystemMessage()</code>  can be handy for quick debugging.</p>\n\n<h3>GetTopScreen()</h3>\n\n<p>Within the context of theming and simfile scripting, calling <code>SCREENMAN:GetTopScreen()</code> will return the screen currently being displayed in the form of a <em>screen</em> object.\n For example, calling it on ScreenGameplay will get you the gameplay screen in the form of a screen object.  Calling it on ScreenPlayerOptions wil do similarly but return the player options screen.</p>\n\n<p>While there are some methods available to <em>all</em> <a data-component=\"Link\" href=\"/LuaAPI#Screens\">screen classes</a> (like <code>AddInputCallback()</code> which is covered in <a href=\"/Arbitrary-Input\">Handling Arbitrary Input</a>), there are many more that depend on the class of the current Screen.\n A screen object of class <em>ScreenGameplay</em> will have the method <code>GetLifeMeter()</code> which returns a <a data-component=\"Link\" href=\"/LuaAPI#Classes-LifeMeter\">LifeMeter</a> object.\n A screen object of class <em>ScreenSelectMusic</em> will have the method <code>GetMusicWheel()</code> which returns a <a data-component=\"Link\" href=\"/LuaAPI#Classes-MusicWheel\">MusicWheel</a> ojbect.</p>\n\n<p>Here is a small example that uses <code>GetTopScreen()</code> on ScreenSelectMusic to get a Lua reference to the MusicWheel and change the current sort of that MusicWheel.  In order for this code to work, it must be called from a theme&apos;s ScreenSelectMusic.</p>\n\n<span class=\"CodeExample-Title\">Using SCREENMAN:GetTopScreen() to get ScreenSelectMusic</span>\n<pre><code class=\"lua\">\nreturn Actor{\n   -- InitCommand happens before the screen we want is the TopScreen,\n   -- so calling GetTopScreen() during an InitCommand would fetch the\n   -- screen being transitioned out of that is about to be destroyed.\n   -- It WILL be ready by the time OnCommand is called, however.\n\n   OnCommand=function(self)\n      -- Since this Lua is being called on ScreenSelectMusic\n      -- the topscreen variable will be a screen object of ScreenSelectMusic.\n      local topscreen = SCREENMAN:GetTopScreen()\n\n      -- Get a Lua reference to the engine's MusicWheel\n      -- This is a Lua object of type \"MusicWheel\"\n      local MusicWheel = topscreen:GetMusicWheel()\n\n      -- Change the current sort of the MusicWheel.\n      -- Fot a full list of available sorts, check the SortOrder enum\n      -- as documented in the Lua API.\n      MusicWheel:ChangeSort(\"SortOrder_Artist\")\n   end\n}\n</code></pre>\n\n<p>Again, there are many custom methods that belong to the many different classes of screens, more than can be reasonably documented here.  For a full list, please refer to the various screen classes documented in the <a data-component=\"Link\" href=\"/LuaAPI\">SM5 Lua API</a>.  This currently includes everything from <strong>Class Screen</strong>:<em>ActorFrame</em> down to <strong>Class ScreenWithMenuElementsSimple</strong>:<em>ScreenWithMenuElements</em> (with the exception of ScreenManager itself, of course!).</p>","/Singletons/SOUND":"<h1>SOUND Singleton</h1>\n\n<p>The SOUND singleton can be used to play audio files from a theme or simfile and has some capabilities that a <a data-component=\"Link\" href=\"/Actors/Sound\">Sound actor</a> lacks.</p>\n\n<h3>SOUND can be used to loop audio files cleanly</h3>\n\n<p>Namely, SOUND can be used to easily and cleanly loop audio files via the <code>PlayMusicPart()</code> method.  Additionally, SOUND has some helpful methods like <code>DimMusic()</code> and <code>StopMusic()</code> that might make it especially interesting from the perspective of a simfile mini-game.</p>\n\n<p>Here is a simple example that could be called from within a simfile&apos;s BGCHANGE or FGCHANGE script.  It assumes that there is a file <em>love-is-war.ogg</em> located in the root of the song directory.  This example uses <code>PlayMusicPart()</code> which accepts eight arguments:</p>\n\n<ul>\n\t<li><strong>music_path</strong> (string)  the path to the audio file you want to load</li>\n\t<li><strong>music_start</strong> (float)  how many seconds into the file you want playback to start</li>\n\t<li><strong>music_length</strong> (float)  how many seconds of the file you want to play</li>\n\t<li><strong>fade_in</strong> (float)  how many seconds should the file fade in over</li>\n\t<li><strong>fade_out</strong> (float)  how many seconds should the file fade out over</li>\n\t<li><strong>loop</strong> (boolean)  set to <code>true</code> if you want the audio file to loop until told to stop</li>\n\t<li><strong>apply_rate</strong> (boolean)  should this audio file follow the engine&apos;s internal sense of music rate?</li>\n\t<li><strong>align_beat</strong> (boolean)  if <code>true</code> or <code>nil</code>, the playback duration is automatically adjusted to cover an integer number of beats</li>\n</ul>\n\n<span class=\"CodeExample-Title\">Simple usage of SOUND singleton:</span>\n<pre><code class=\"lua\">\nreturn Def.Actor{\n   Name=\"BGM\",\n   OnCommand=function(self, params)\n      local directory = GAMESTATE:GetCurrentSong():GetSongDir()\n      local path = directory .. \"love-is-war.ogg\"\n\n      -- love-is-war.ogg is 4 minutes and 10 seconds in duration.\n      -- We want it to loop.\n      SOUND:PlayMusicPart(path, 0, 250.4, 0, 0, true, true, true)\n\n      -- Wait 10 seconds, then queue a command where we'll\n      -- lower the playback volume\n      self:sleep(10):queuecommand(\"LowerPlaybackVolume\")\n   end,\n\n   LowerPlaybackVolumeCommand=function(self)\n      -- Lower the volume to 33% for a duration of 10 seconds.\n      SOUND:DimMusic(0.33, 10)\n   end,\n\n   OffCommand=function(self)\n      -- We don't want this audio to continue playing past\n      -- ScreenGameplay, so, when this actor's OffCommand\n      -- is triggered, stop the SOUND singleton.\n      SOUND:StopMusic()\n   end\n}\n</code></pre>\n\n<h3>SOUND or Def.Sound&#123;&#125; ?</h3>\n\n<p>Of course, the SOUND singleton lacks some of the special features that a <a data-component=\"Link\" href=\"/Actors/Sound\">Sound actor</a> possesses.</p>\n\n<p>Only one sound can be played via SOUND at any given moment, while there can be multiple <em>Sound</em> actors loaded simultaneously.  Furthermore, SOUND has no control over playback pitch or stereo panning.  Thus, both the SOUND singleton and <em>Sound</em> actors have unique and valid use cases.</p>","/Introduction/Lua":"<h1>Differences between Lua 5.0 and Lua 5.1</h1>\n\n<p>SM3.95 used <a href=\"http://www.lua.org/versions.html#5.0\">Lua 5.0</a>, while SM5 currently uses <a href=\"http://www.lua.org/versions.html#5.1\">Lua 5.1</a>.\nThe primary differences that will likely impact your scripting experience are:</p>\n\n\n<h3>Modulus Operator as %</h3>\n\n<p>Lua 5.0 required <code>math.mod()</code> to perform modulo operations, while Lua 5.1 introduced a proper modulus operator.</p>\n\n<span class=\"CodeExample-Title\">Modulus operator example:</span>\n<pre><code class=\"lua\">\nlocal teamdragonforce = 3 % 2\nprint(teamdragonforce)\n-- output: 1\n</code></pre>\n\n\n<h3>Table Size Operator as &#35;</h3>\n\n<p>Lua 5.0 required the  <code>table.getn()</code> function to determine the size of an indexed table, while Lua 5.1 introduced a simple operator for this common task.</p>\n\n<span class=\"CodeExample-Title\">Indexed table size operator:</span>\n<pre><code class=\"lua\">\n-- this table is indexed, so the # operator works\nlocal StomperZ = { \"fast\", \"brutal\", \"bearlike\" }\nprint(#StomperZ)\n-- output: 3\n\n-- this table is key/value, so the # operator does NOT work\nlocal NotIndexed = {\n   blah = \"fast\",\n   nah = \"brutal\",\n   whatever = \"bearlike\"\n}\nprint(#NotIndexed)\n-- output: 0\n</code></pre>\n\n\n\n<h3>Simple For Loop Deprecation</h3>\n\n<p>For the official explanation, read <a href=\"http://www.luafaq.org/#T1.13\">http://www.luafaq.org/#T1.13</a></p>\n\n<hr />\n\n<h2>&lt;XML&gt; in SM3.95 converted to &#123; Lua &#125; in SM5</h2>\n\n<p>A typical SM3.95 XML file would feature an ActorFrame element which would contain various children Actors.  This model is conceptually the same in SM5, but uses <code>.lua</code> files instead of <code>.xml</code> files.  So, where Actors were represented by <em>XML elements</em> in SM3.95, they are <strong>Lua tables</strong> in SM5.</p>\n\n\n\n<h3>An Example SM3.95 XML File</h3>\n\n<p>SM3.95 had 17 unique Actor classes, each of which could be invoked with a corresponding XML element.</p>\n\n<p>A simple XML file containing some Actors commonly used in SM3.95 might look like:</p>\n\n<span class=\"CodeExample-Title\">A typical SM3.95 XML script:</span>\n<pre><code class=\"xml\">\n&lt;!-- ActorFrame is an Actor --&gt;\n&lt;ActorFrame OnCommand=&quot;x,SCREEN_CENTER_X;y,SCREEN_CENTER_Y;sleep,9999&quot;&gt;\n&lt;children&gt;\n\n&lt;!-- Quad is an Actor --&gt;\n&lt;Quad\n   InitCommand=&quot;zoomto,50,137&quot;\n/&gt;\n\n&lt;!-- BitmapText is also an Actor --&gt;\n&lt;BitmapText\n  File=&quot;_eurostile normal&quot;\n  Text=&quot;Eat Poptarts.&quot;\n/&gt;\n\n&lt;!-- Sprite is, that&#x27;s right, an Actor --&gt;\n&lt;Sprite\n  Texture=&quot;chrismarks.png&quot;\n  Frame0000=&quot;0&quot;\n/&gt;\n\n&lt;/children&gt;\n&lt;/ActorFrame&gt;\n</code></pre>\n\n\n<h3>The Example File Converted to SM5 Lua</h3>\n\n<p>In SM5, these same Actors exist (and quite a few more; 43 in total!), but the means of invoking them has changed since pure Lua files have replaced XML.  For a complete list of SM5 Actors, see <a href=\"https://github.com/stepmania/stepmania/blob/master/Docs/Themerdocs/actordef.txt\">./Docs/ThemerDocs/actordef.txt</a></p>\n\n<p>The most commonly used Actors will each be discussed in detail in the following sections.  For now, we&apos;ll start by looking at the <em>SimpleExample.xml</em> if we were to convert it to SM5 Lua:</p>\n\n<span class=\"CodeExample-Title\">The same script written in SM5 Lua:</span>\n<pre><code class=\"lua\">\n--start by defining an ActorFrame\n--we'll call it ExampleAF (example ActorFrame)\nlocal ExampleAF = Def.ActorFrame{\n   OnCommand=function(self)\n      self:Center():sleep(9999)\n   end,\n\n   -- since Actors are just Lua tables\n   -- we can nest them directly inside\n   -- the parent ActorFrame table like this\n   Def.Quad{\n      InitCommand=function(self)\n        self:zoomto(50,137)\n      end\n   },\n\n   -- note that since these are elements in\n   -- a Lua table, everything is comma-delimited\n   Def.BitmapText{\n      Font=\"Common Normal\",\n      Text=\"Eat Poptarts.\"\n   }\n}\n\n--since ExampleAF is an indexed Lua table, we can also\n--add elements to its next index externally like this:\nExampleAF[#ExampleAF + 1] = Def.Sprite{\n   Texture=\"chrismarks.png\"\n}\n\n--Every Lua Theme/BGCHANGE/FGCHANGE file in SM5 must return some\n--sort of Actor. Typically, we end up returning an ActorFrame.\nreturn ExampleAF\n</code></pre>\n\n\n\n<h3>Dynamically Adding Actors in SM5</h3>\n<p>We can take advantage of the idea that SM5 uses the Lua rather than XML to dynamically add actors to an ActorFrame.  Depending on the scenario and the number of Actors being added to an ActorFrame, this can <em>greatly</em> reduce the lines of code you&apos;ll need to write.</p>\n\n<p>Imagine that you have a single png asset, <em>dog.png</em> and you want five instances of it to appear on the screen. You&apos;d like the end result to look like this:</p>\n\n<p><img alt=\"\" class=\"img-fluid\" src=\"/Lua-For-SM5/img/dogs.png\" /></p>\n\n<p>The basic idea is the same regardless of whether you are using SM3.95 or SM5: you&apos;ll need to define five <Link to=\"/Def.Sprite\">Sprite Actors</Link> within your main ActorFrame.  To achieve this in SM3.95&apos;s XML, you could do something like this:</p>\n\n<span class=\"CodeExample-Title\">Five Dogs in XML:</span>\n<pre><code class=\"xml\">\n&lt;ActorFrame OnCommand=&quot;sleep,9999&quot;&gt;\n&lt;children&gt;\n   &lt;Sprite\n      Name=&quot;sprite 1&quot;\n      Texture=&quot;./dog.png&quot;\n      InitCommand=&quot;x,50;y,50&quot;\n   /&gt;\n   &lt;Sprite\n      Name=&quot;sprite 2&quot;\n      Texture=&quot;./dog.png&quot;\n      InitCommand=&quot;x,150;y,150&quot;\n   /&gt;\n   &lt;Sprite\n      Name=&quot;sprite 3&quot;\n      Texture=&quot;./dog.png&quot;\n      InitCommand=&quot;x,250;y,250&quot;\n   /&gt;\n   &lt;Sprite\n      Name=&quot;sprite 4&quot;\n      Texture=&quot;./dog.png&quot;\n      InitCommand=&quot;x,350;y,350&quot;\n   /&gt;\n   &lt;Sprite\n      Name=&quot;sprite 5&quot;\n      Texture=&quot;./dog.png&quot;\n      InitCommand=&quot;x,450;y,450&quot;\n   /&gt;\n&lt;/children&gt;\n&lt;/ActorFrame&gt;\n</code></pre>\n\n<p>That&apos;s about 30 lines of code already to basically do nearly the same thing five times.  If you&apos;re thinking that a <strong>loop</strong> might be handy here, you&apos;re on <em>exactly</em> the right track!  Let&apos;s rewrite that XML to be Lua for SM5.</p>\n\n<span class=\"CodeExample-Title\">Five Dogs in Lua:</span>\n<pre><code class=\"lua\">\nlocal af = Def.ActorFrame{\n   OnCommand=function(self) self:sleep(999) end\n}\n\n-- loop from 0 to 4 and add a Sprite Actor to the af ActorFrame each time\nfor i=0,4 do\n   af[#af+1] = LoadActor(\"./dog.png\")..{\n      Name=\"sprite \" .. i+1,\n      InitCommand=function(self)\n         self:xy(i*100+50, i*100+50)\n      end\n   }\nend\n\nreturn af\n</code></pre>\n\n<p>We were able to accomplish the same effect using a <strong>numerical for loop</strong> in SM5&apos;s Lua in only 15 lines!</p>\n\n<p>This is a modest improvement already, but imagine if we wanted to add 100 or even 1000 instances of <em>dog.png</em> to the screen.  With XML, it is necessary to explicitly define each Actor you want in your main ActorFrame, while with Lua, it is only necessary to increase the loop&apos;s stop value from <code>4</code> to a different number.</p>","/Introduction/Foreword":"<h1>What are Actors?</h1>\n\n<h2>Tables Within Tables Within...</h2>\n\n<p>An Actor is the most basic unit of StepMania scripting.  Virtually everything else that you work with in SM5 Lua, be it a Sprite, a BitmapText, or an ActorMultiVertex, is built on top of the foundation that Actor provides.</p>\n\n<p>From a scripter&#8217;s perspective, an Actor is a Lua table with a few special properties that the StepMania engine will look for.  Other StepMania elements that derive from Actor are also really just Lua tables as well.</p>\n\n<p>So if you are familiar with <em>ActorFrames</em>, the StepMania element that serves as a container for manipulating multiple Actors simultaneously within itself, you can start to appreciate how an ActorFrame is a table that contains Actors which are tables.</p>\n\n<p>It&#8217;s tables all the way down.</p>\n\n<h2>Types of Actors</h2>\n\n<p>There are 43 specialized types of Actors available to StepMania&#8217;s Lua scripting interface.  Each type has unique methods available to it and a unique use-case.  For example,</p>\n\n<ul>\n\t<li><code>Quad</code> actors are quadrilaterals that you can programmatically manipulate</li>\n\t<li><code>BitmapText</code> actors are used to draw text to the screen</li>\n\t<li><code>ActorMultiVertex</code> actors are arbitrary polygons that you can programmatically manipulate</li>\n\t<li><code>Sprite</code> actors are used to load visual assets like png images or avi movies</li>\n\t<li><code>ActorFrame</code> actors serve as containers for other actors, and are useful when you want to manipulate a group of actors simultaneously, rather than individually</li>\n\t<li>etc.</li>\n</ul>\n\n<p>For a complete list of SM5 Actor types, see <a href=\"https://github.com/stepmania/stepmania/blob/master/Docs/Themerdocs/actordef.txt\">./Docs/ThemerDocs/actordef.txt</a></p>\n\n<h2>An Example SM5 Lua Script</h2>\n\n<p>The most commonly used Actors will each be discussed in detail in the chapters under <strong>Actors</strong> in the navigation menu.  For now, we&#8217;ll start by looking at a simple Lua script for StepMania 5.</p>\n\n\n<span class=\"CodeExample-Title\">A Simple SM5 Lua Script:</span>\n<pre><code class=\"lua\">\n-- start by defining an ActorFrame\n-- call it ExampleAF (example ActorFrame)\nlocal ExampleAF = Def.ActorFrame{\n   OnCommand=function(self)\n      self:Center():sleep(9999)\n   end,\n\n   -- since Actors are just Lua tables\n   -- we can nest them directly inside\n   -- the parent ActorFrame table like this\n   Def.Quad{\n      InitCommand=function(self)\n         self:zoomto(50,137)\n      end\n   },\n\n   -- note that since these are elements in\n   -- a Lua table, everything is comma-delimited\n   Def.BitmapText{\n      Font=\"Common Normal\",\n      Text=\"Eat Poptarts.\"\n   }\n}\n\n\n-- since ExampleAF is an indexed Lua table, we can also\n-- add elements to its next index externally like this:\nExampleAF[#ExampleAF + 1] = Def.Sprite{\n   Texture=\"chrismarks.png\"\n}\n\n-- Every Lua Theme/BGCHANGE/FGCHANGE file in SM5 must return some\n-- sort of Actor. Typically, we end up returning an ActorFrame.\nreturn ExampleAF\n</code></pre>\n\n\n<h2>Only one Actor can be returned per file.</h2>\n\n<p>The StepMania engine expects one Actor to be returned per Lua file, yet the example above features four Actors:</p>\n\n<ul>\n\t<li>1 ActorFrame</li>\n\t<li>1 Quad</li>\n\t<li>1 BitmapText</li>\n\t<li>1 Sprite</li>\n</ul>\n\n<p>How do we make this work?</p>\n\n<p>The solution reveals a prominent design paradigm in StepMania scripting  put all other Actors inside the ActorFrame and return the ActorFrame.  An ActorFrame is just one type of Actor, and any valid Actor may be returned.</p>\n\n<p>If your Lua file is <em>very</em> simple, you may only have one Actor, maybe a Sprite or a Quad.  In such situations, it is not necessary to wrap the single Actor in an ActorFrame; <em>any valid StepMania Actor may be returned.</em></p>\n\n<p>Still, most files end up being complex enough to warrant using an ActorFrame.</p>","/Introduction/Mod-Chart-Setup":"<h1>Setting Up a \"Mod\" Chart</h1>\n<p>\n   StepMania simfiles can be scripted to enhance gameplay using Lua and the SM5 API. This chapter will briefly demonstrate how to prepare a simfile for such scripting.\n</p>\n<p class=\"alert alert-warning\">\n   This chapter is a work-in-progress!\n</p>\n<p>\n   The FGCHANGES field in your .sm file accepts several arguments delimited by equals signs. The first two are the most important:\n</p>\n<ul>\n   <li>the beat in Gameplay at which you want the Lua file to be activated</li>\n   <li>a path to the Lua file where you will be writing your code</li>\n</ul>\n<p>\n   So, for exmaple, if you named your file <em>default.lua</em> and wanted it to trigger at beat 4, you would need to change your .sm (or .ssc) file to include:\n</p>\n<pre><code>#FGCHANGES: 4.000=./default.lua=1.000=0=0=1=====;</code></pre>\n<p>\n   The other arguments (the <code>1.000</code> and subsequent <code>0</code>s and <code>1</code>s) are <a href=\"https://github.com/stepmania/stepmania/wiki/sm#bgchanges\">various flags that are basically vestiges</a> from SM3.9 that are necessary to include but will have no impact on your Lua. For FGCHANGES, you can just copy/paste them into your .sm file for each FGCHANGE.\n</p>\n<p>\n   Any Lua-based actors loaded as FGCHANGES will be cleared from the screen as soon as they are \"done.\" This means that if the actor isn't actively <a data-component=\"Link\" href=\"/Examples/Simple-Tweens\">tweening</a> in some way, it will be cleared from memory.\n</p>\n<p>\n   A common strategy is to ensure that this doesn't occur prematurely is to add a dummy keep-alive Actor that does nothing but sleep for as long as you want the overall Lua file to persist for. That might look like:\n</p>\n<pre><code class=\"lua\">\n-- a Lua file can only return one Actor\n-- so the common strategy is to return one ActorFrame that contains many sub-Actors\nreturn Def.ActorFrame{\n   -- keep-alive Actor\n   -- this will allow the file to persist for 999 seconds\n   -- (or, until the end of the stepchart)\n   Def.Actor{ OnCommand=function(self) self:sleep(999) end },\n\n   -- the Sprite Actor\n   Def.Sprite{\n      Texture=\"awesome.png\",\n      OnCommand=function(self)\n         self:Center():FullScreen():sleep(3):diffusealpha(0)\n      end\n   }\n}\n</code></pre>\n<p>\n   In the example above, the the Sprite has a <code>sleep(3)</code> tween, so it would appear on the screen for 3 seconds at beat 4 (specified by the .sm file) and then have an alpha of <code>0</code> applied, causing it to be effectively invisible.\n</p>\n<p>\n   If you are testing this in SM5's editor, you'll need to ensure that you have <strong>Show Background Changes</strong> enabled, otherwise neither BGChanges nor FGChanges will appear.\n</p>\n<p>\n   SM5's Lua error reporting interface is invaluable when scripting things like this. You can toggle it on/off by holding <kbd>F3</kbd> and tapping <kbd>F6</kbd> then tapping <kbd>8</kbd>\n</p>\n<p>\n   Finally, if there are no Lua errors, and you still can't get anything to appear, you may need to reload your simfiles.\n</p>","/Introduction/Supported-File-Extensions":"<h1>Supported File Extensions</h1>\n\n<p>In StepMania 5, the supported file extensions are listed in <a href=\"https://github.com/stepmania/stepmania/blob/5_1-new/src/ActorUtil.cpp#L521-L572\">/src/ActorUtil.cpp</a>:</p>\n\n<ul>\n\t<li><strong>Images:</strong> bmp, gif, jpeg, jpg, png</li>\n\t<li><strong>Audio:</strong> mp3, oga, ogg, wav</li>\n\t<li><strong>Video:</strong> avi, f4v, flv, mkv, mp4, mpeg, mpg, mov, ogv, webm, wmv</li>\n\t<li><strong>3D Models:</strong> txt</li>\n</ul>\n\n<hr />\n\n<h3>Extra Notes on Filetypes:</h3>\n\n<h4>Images:</h4>\n\n<p><em>.png</em> files with even dimensions are strongly preferred.</p>\n\n<h4>Audio:</h4>\n\n<p><em>.ogg</em> files are strongly preferred; variable-bitrate mp3 files <a href=\"https://github.com/stepmania/stepmania/issues/369\">are buggy</a>.</p>\n\n<h4>Video:</h4>\n\n<p>SM5 supports a broad array of codecs and containers via <a href=\"https://www.ffmpeg.org/\">ffmpeg</a>.</p>\n\n<p>Please note that you cannot play audio from video files. One workaround is to extract the audio from the video, and play it at the same time as the video.</p>\n\n<p>You are generally safe to use HD video without performance issues if the computer is from the last six or seven years. The <em>.avi</em> container in conjunction with the h264 codec works great.  As of this writing, the <strong>h265 codec is not supported</strong> by SM5&apos;s version of ffmpeg.</p>\n\n<p><a href=\"https://handbrake.fr/\">Handbrake</a> is a useful, free application you can use to resize your videos and convert them to one of the aforementioned formats with the h264 codec.</p>\n\n<h4>3D Models:</h4>\n\n<p>3D Models still rely on MilkShape 3D ASCII text as they have since StepMania 3.9.  Documentation is scarce at best.  Sorry.</p>"}